<template>
  <div>
      <van-image v-for="(item,index) in subData.answerCard" 
          :key="index" width=100% 
          :src="item"
          @click="imagePreview(subData.answerCard,index)">
          <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template>
        </van-image>
      <div class="table">
        <div style="height:10px;"/>
        <div class="questions">
            <div>题号</div>
            <div>我的答案</div>
            <div>标准答案</div>
            <div>得分</div>
        </div>
        <div class="questions" v-for="(item,index) in subData.question.THs" :key="index" v-if="index<maxExpand">
            <p :style="viewWrongQuestion(item)">{{item.TH}}</p>
            <p :style="viewWrongQuestion(item)">{{item.Content}}</p>
            <p :style="viewWrongQuestion(item)">{{item.trueAnswer}}</dipv>
            <p :style="viewWrongQuestion(item)">{{item.Score + "/" + item.totalScore}}</p>
        </div>
        <div class="expand" @click="onExpand">
          <div v-if="maxExpand<subData.question.THs.length"><van-icon name="arrow-down" size="20"/>展开</div>
          <div v-else><van-icon name="arrow-up" size="20"/>收起</div>  
        </div>      
      </div>
      <div style="height: 3rem;"/>
    </div>
  </div>
</template>

<script>
import { ImagePreview } from 'vant';
export default{
  props:['subData'],
  data () {
    return {
      maxExpand:5,
    };
  },
  methods:{
    viewWrongQuestion(item){
      return item.Score==item.totalScore?'':'color:red;';
    },
    imagePreview(images,index){
      ImagePreview({images:images,showIndex:true,loop:false,maxZoom:5,startPosition:index});
    },
    onExpand(){
      if(this.maxExpand>=this.subData.question.THs.length)
        this.maxExpand = 5;
      else
        this.maxExpand =this.subData.question.THs.length;
    },
  },
};
</script>

<style lang="less" scoped>
.table{
  border-radius: 10px;
  background-color: rgb(221, 221, 221);
  margin: 20px 10px;
  color:black;
}
.questions{
  display: grid;
  justify-items:center;
  align-items:center;
  width: 100%;
  height: 30px;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto auto;
}
.expand{
  height: 30px;
  text-align: center;
}
</style>



