<template>
  <div>
    <loading :show="isLoading" />
    <vehicle-header 
                noSearch
                title="成绩详情"
                iconName =""/>

    <van-tabs v-show="!isLoading">
      <van-tab v-for="(item,index) in subjects" :key="index" :title="item.km">
        <subject :subjects="subjects" :subData="item"/>
      </van-tab>  
    </van-tabs>
    <div style="height: 3rem;"/>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import Subject from "@/views/Subject.vue"
import Loading from "@/components/Loading.vue"
import Vue from 'vue';
export default {
  components: {
    "vehicle-header": Header,
    "subject":Subject,
    "loading":Loading,
  },
  data () {
    return {
      examData:{examName:''},
      subjects:[],
      isLoading:false,
    };
  },
  mounted(){
    this.isLoading = true;
    this.getSubject();
  },
  watch:{
    '$route'(to,from){
      if(this.$route.query.examGuid != undefined){
        this.isLoading = true;
        this.getSubject();  
      }
    }
  },
  methods: {
    getSubject(){
      this.subjects = [];
      this.examData = this.$route.query;
      let that = this;
      this.zone.querySubjectInfo(this.examData).then(ret =>{
        if(ret.status == 200 && ret.data.status == 200){
          that.subjects = ret.data.data.subjects;
          let idx=0;
          that.subjects.forEach(el=>{
            that.zone.querySubjectGrade(that.examData,ret.data.data,el).then(sub =>{
              if(sub.status == 200 && sub.data.status == 200){
                that.$set(el,"level",sub.data.data.report.grade);
                that.$set(el,"rating",sub.data.data.report.rating);
                that.$set(el,"total",sub.data.data.report.total);                                                 
              }
            });
            if(el.question != undefined && el.question.asiresponse != undefined){
              that.zone.queryAnswerCard(that.examData,ret.data.data,el).then(ans =>{
                if(ans.status == 200 && ans.data.status == 200){
                  that.$set(el,"answerCard",ans.data.data);                                               
                }
              });
            }
            idx++;
          });
          that.isLoading = false;
        }else{
          if(ret.data.message != undefined)
            that.$toast(ret.data.message);
          else
            that.$toast(ret.message);
          that.$router.push({path:"/login"});
        }

      });
    },
  },
};
</script>



